.plans {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 320px;
  margin-top: 22px;
}

.plans__list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;

  list-style: none;

  .zero-margin();
}

.plans__item {
  margin: 0 20px;
  margin-bottom: 20px;
}

.plans__card {
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: @tablet-width) {
  .plans {
    width: @tablet-width;
    margin: 0 auto;
    margin-top: 529px;
  }

  .plans__item {
    margin: 0 20px;
    margin-bottom: 30px;
  }
}

@media (min-width: @desktop-width) {
  .plans {
    width: @desktop-width;
    margin: 0 auto;
    margin-top: 80px;
  }

  .plans__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
  }

  .plans__item {
    margin: 0 40px;
    margin-bottom: 30px;
  }
}
