.quest-form {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 320px;
}

.quest-form__heading {
  font-size: 36px;
  line-height: 1.67;
  .zero-margin();
  margin-top: 15px;
  margin-left: 20px;
  color: @heading-color;
}

.quest-form__subtitle {
  width: 230px;
  margin-top: 25px;
  margin-left: 20px;
  margin-bottom: 25px;
  text-transform: uppercase;
  font-family: @font-oswald-r;
  font-size: 14px;
  line-height: 1.29;
  color: @heading-color;
}

@media (min-width: @tablet-width) {
  .quest-form {
    width: @tablet-width;
    margin: 0 auto;
  }

  .quest-form__heading {
    font-size: 60px;
    line-height: 1;
    margin-top: 40px;
    margin-left: 29px;
  }

  .quest-form__subtitle {
    width: 325px;
    margin-left: 70px;
    margin-top: 56px;
    margin-bottom: 46px;
    font-size: 20px;
    line-height: 1.5;
  }
}

@media (min-width: @desktop-width) {
  .quest-form {
    width: @desktop-width;
    margin: 0 auto;
  }

  .quest-form__heading {
    font-size: 60px;
    line-height: 1;
    margin-top: 59px;
    margin-left: 40px;
  }

  .quest-form__subtitle {
    width: 325px;
    margin-left: 40px;
    margin-top: 55px;
    margin-bottom: 46px;
    font-size: 20px;
    line-height: 1.5;
  }
}
