.page-header {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 320px;
  margin: 0 auto;
  border-bottom: 1px solid #dcdcdc;
}

.page-header__logo-link {
  display: block;
  margin-top: 11px;
  padding-bottom: 15px;
  margin-left: 20px;
  width: 191px;
  height: 39px;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.page-header__logo {
  width: 191px;
  height: 39px;
}

.page-header--index {
  &::after {
    display: none;
    content: "";
    position: absolute;
    left: 0;
    min-height: 306px;
    width: 100%;
    background-image: url("../img/index-background-mobile@1x.jpg");
    background-color: #63a73a;
    background-position: bottom;
    background-repeat: no-repeat;
    z-index: -1;
  }
}

@media (min-width: @tablet-width) {
  .page-header {
    justify-content: space-between;
    flex-direction: row;
    width: @tablet-width;
    margin: 0 auto;
    border: none;
  }

  .page-header__logo-link {
    margin-top: 24px;
    margin-left: 29px;
    width: 174px;
    height: 60px;
  }

  .page-header__logo {
    width: 174px;
    height: 60px;
  }

  .page-header--index {
    &::after {
      display: none;
    }
  }
}

@media (min-width: @desktop-width) {
  .page-header {
    justify-content: space-between;
    width: @desktop-width;
    margin: 0 auto;
  }

  .page-header__logo-link {
    margin-top: 54px;
    margin-left: 40px;
    width: 203px;
  }

  .page-header__logo {
    width: 203px;
    height: 60px;
  }

  .page-header--index {
    &::after {
      display: block;
      width: 50%;
      min-height: 696px;
      top: 0;
      right: 0;
      left: inherit;
      background-image: url("../img/index-background-desktop@1x.jpg");
      background-color: #63a73a;
      background-position: 0%;
      background-repeat: no-repeat;
      background-size: cover;

      @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
        background-image: url("../img/index-background-desktop@2x.jpg");
      }
    }
  }
}
