.main-nav {
  display: flex;
  width: 320px;
  justify-content: flex-start;
  flex-direction: column;
  background-color: #ffffff;
}

.main-nav__toggle {
  display: block;
  width: 65px;
  height: 65px;
  background-color: transparent;
  border: none;
  margin-top: -65px;
  margin-left: auto;
  margin-right: 0;
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  .zero-margin();
  width: 100%;
  list-style: none;
}

.main-nav__item {
  background-color: #ffffff;
}

.main-nav__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 65px;
  font-family: @font-oswald-r;
  font-size: 20px;
  font-weight: 500;
  line-height: 1;
  color: @heading-color;
  text-decoration: none;
  text-transform: uppercase;
  border-top: 1px solid #f2f2f2;
  border-bottom: 1px solid #f2f2f2;
  cursor: pointer;

  &:hover {
    color: rgba(17, 17, 17, 0.6);
  }

  &:active {
    color: rgba(17, 17, 17, 0.3);
  }
}

@media (max-width: 767px) {
  .main-nav {
    &--closed .main-nav__toggle::before {
      content: "";
      display: block;
      width: 25px;
      height: 19px;
      margin-left: 14px;
      background-image: url("../img/icon-mobile-menu.svg");
      background-color: #ffffff;
      background-repeat: no-repeat;
    }

    &--closed .main-nav__toggle:active::before {
      opacity: 0.3;
    }

    &--opened .main-nav__toggle {
      &::before {
        content: "";
        display: block;
        width: 19px;
        height: 19px;
        margin-left: 17px;
        background-image: url("../img/icon-close.svg");
        background-color: #ffffff;
        background-repeat: no-repeat;
      }

      &:active::before {
        opacity: 0.3;
      }
    }

    &--closed .main-nav__list {
      display: none;
    }

    &--nojs .main-nav__toggle {
      display: none;
    }
  }
}

@media (min-width: @tablet-width) {
  .main-nav {
    width: 490px;
    margin-right: 30px;
    background-color: transparent;
  }

  .main-nav__toggle {
    display: none;
  }

  .main-nav__list {
    width: 490px;
    flex-direction: row;
    justify-content: flex-end;
    background-color: transparent;
  }

  .main-nav__item {
    background-color: transparent;
    margin-left: 36px;

    &:first-of-type .main-nav__link--index {
      padding-bottom: 9px;
      border-bottom: 2px solid @btn-bg;
    }
  }

  .main-nav__link {
    height: 18px;
    font-size: 18px;
    font-weight: normal;
    line-height: 1.39;
    color: @label-color;
    border-top: none;
    border-bottom: none;
    margin-top: 45px;

    &--active {
      padding-bottom: 11px;
      border-bottom: 2px solid @btn-bg;
    }
  }
}

@media (min-width: @desktop-width) {
  .main-nav {
    min-width: 650px;
    margin-right: 0;
    margin-left: auto;
    background-color: transparent;
  }

  .main-nav__list {
    width: 610px;
    flex-direction: row;
    margin-right: 40px;
    justify-content: flex-end;
    background-color: transparent;
  }

  .main-nav__item {
    background-color: transparent;
    margin-left: 34px;

    &:first-of-type .main-nav__link--index {
      padding-bottom: 11px;
      border-bottom: 2px solid #ffffff;
    }
  }

  .main-nav__link {
    height: 20px;
    font-size: 20px;
    font-weight: normal;
    line-height: 1.25;

    color: @heading-color;
    margin-top: 75px;

    &:hover {
      color: rgba(17, 17, 17, 0.6);
    }

    &:active {
      color: rgba(17, 17, 17, 0.3);
    }

    &--index {
      color: #ffffff;

      &:hover {
        color: rgba(255, 255, 255, 0.6);
      }

      &:active {
        color: rgba(255, 255, 255, 0.3);
      }
    }

    &--active {
      padding-bottom: 11px;
      border-bottom: 2px solid @btn-bg;
    }
  }
}
